  <van-tabs :background="'#F5F6FE'" :color="'#1baeae'" :title-active-color="'#1baeae'"
             v-model="status">
    <van-tab title="周">
      <FoodRecord type="week" />
    </van-tab>
    <van-tab title="月">
      <FoodRecord type="month" />
    </van-tab>
    <van-tab title="年">
      <FoodRecord type="year" />
    </van-tab>
  </van-tabs>

</template>

<script>
import sHeader from '@/components/SimpleHeader'

import FoodRecord from './momtion-record'
import {reactive, onMounted, ref, toRefs } from 'vue'

export default {
  name: "index",
  components: {
    FoodRecord,
    sHeader,
  },
  setup() {

    onMounted(
        () => {
        }
    )

    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      todo: '',
    })
    const active = ref(2);
    // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
    return {
      ...toRefs(state),
      active,
    }
  }
}
</script>

