<template>
  <div :style="{ backgroundColor: '#F5F6FE',height:'100vh',display: 'flex', flexDirection: 'column', paddingBottom: '10px' }">

    <div class="margin_box" :style="{ marginTop: '10px',paddingTop: '5px' }">
      <!--选择日期-->
      <change-date :style="{ backgroundColor: '#fff'}" :selectdate="selectdate" :type="type" :onchange="onchange"/>
      <!--平均摄入热量-->
      <div class="week_drink_num">
        <p>{{ sumCount }}千卡</p>
        <p>平均日消耗热量</p>
      </div>
    </div>
    <!--本周数据-->
    <div class="d_h_r_item">
      <span class="span_drinkadd_title">{{ title }}</span>
      <label class="lable_item">图形分析:</label>
      <van-switch v-model="checked"  @update:model-value="onUpdateValue"  size="16px"  active-color="#15AC78" inactive-color="#dcdee0"/>
    </div>
    <!--  历史记录  -->
    <div class="margin_box scroll_box" :style="{ borderTopLeftRadius: 0, borderTopRightRadius: 0}"  >
      <HistoryRecord  v-if="!checked" :list="list"  :type="type" :toYearDetial="toYearDetial"/>
      <LineECharts v-else-if="checked" :list="type === 'year' ? yearLineList : list"/>
    </div>

  </div>

</template>

<script>
import moment from 'moment'
import  ChangeDate from '../../../components/ChangeDate'
import  HistoryRecord from '../../components/history-record'
import   LineECharts from '../../components/line-chart'
import { onMounted, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import {getSportTypeList, getSyLogSportHistoryByTime} from "@/service/motion"
import { formatYYMMDD, formatMMTOHHMM, getSportObj } from "@/utils/util";
import {useStore} from "vuex";
export default {
  name: "momtion-record",
  components: {
    ChangeDate,
    HistoryRecord,
    LineECharts
  },
  props:{
    type: {
      default:'week',
      type: String
    },
  },
  setup(props){
    const router = useRouter()
    const store = useStore()

    onMounted(
        () => {
          // 赋值
          // state.list = getList()
          requestList()
          requestSport()
          state.title = props.type === "week" ? "本周数据" :  props.type === "month" ? "本月数据"  : "本年数据"
        }
    )

    // 获取运动类型
    const requestSport = async () => {
      const res = await getSportTypeList()
      const { code, data = []} = res
      code === 200 && store.commit('sportList',data)
      const sportObj = getSportObj(data)
      store.commit('sportTypeObj',sportObj)
    }

    const requestList = async () => {
      const startTime =  getStartTime(props.type)
      const  res = await getSyLogSportHistoryByTime(startTime,formatYYMMDD(state.selectdate))
      const { code, data = {} } = res
      const listData = data
      if(code === 200){
        const newList = []
        Object.keys(listData).forEach((key)=>{
          const item = listData[key]
          let stepNumberSum = 0, distanceSum = 0, heatSum = 0,sportTimeSum = 0
          console.log("item",item)

          item.map(item => {
            heatSum = heatSum + item.heat
            sportTimeSum = sportTimeSum + item.sportTime
            stepNumberSum =stepNumberSum + item['stepNumber']
            distanceSum = distanceSum + item['distance']
          })

          const mDate = {
            heatSum, // 总热量
            stepNumberSum, // 总步数
            distanceSum, // 总距离
            sportTimeSum: formatMMTOHHMM(sportTimeSum), // 总时长
            list: item,
            date: moment(Number(key) ).format("YYYY-MM-DD") ,
            ...item
          }
          console.log("mDatemDatemDatemDate",mDate)
          mDate.yearDate = moment(Number(key)).format("YYYY-MM")
          newList.push(mDate)
        })
        if(props.type === "year"){
          state.yearLineList = newList
        }
        state.list = props.type === "year" ? handleYearList(newList) : newList
        state.sumCount = handleAverageValue(newList,props.type === "week" ? 7 : props.type === "year" ? 365 : 30 )
        console.log("获取食物列表--->", newList)
      }
    }

    //处理年数据
    const handleYearList = (list) => {
      const afterData = [] // 数组
      const beforeData = list// 数组
      let tempArr = [];
      for (let i = 0; i < beforeData.length; i++) {
        if (tempArr.indexOf(beforeData[i].yearDate) === -1) {
          afterData.push({
            yearDate: beforeData[i].yearDate,
            origin: [beforeData[i]]
          });
          tempArr.push(beforeData[i].yearDate);
        } else {
          for (let j = 0; j < afterData.length; j++) {
            if (afterData[j].yearDate == beforeData[i].yearDate) {
              afterData[j].origin.push(beforeData[i]);
              break;
            }
          }
        }
      }
      console.log('年数据处理------', afterData)
      return afterData
    }

    // 求摄入热量平均值(年、周、月)
    const handleAverageValue = (list,days) => {
      const NumList = []
      list.map((item)=>{
        NumList.push(Number(item.heatSum))
      })
      const sum =  NumList.reduce(function(sum,number){ //sum2 前两个数的和
        return sum + number;
      },0)
      const averageValue = sum/days || 0
      return averageValue.toFixed(2)
    }


    // 开始时间
    const getStartTime = (type) => {
      let startTime = ""
      if(state.endDate){
        return  moment(state.endDate).format("YYYY-MM-DD")
      }
      let date = new Date(state.selectdate)
      switch (type){
        case 'week':
          startTime = date.setDate(date.getDate() - 7)
          break
        case 'month':
          startTime = date.setMonth(date.getMonth() - 1);
          break
        case 'year':
          startTime = date.setFullYear(date.getFullYear() - 1);
          break
      }
      return  moment(startTime).format("YYYY-MM-DD")
    }

    const getList = async ()=>{
      const mlist = []
      for (let i = 0; i < 5; i++) {
        mlist.push({
          date: `2021-8-${Number(13+i)}`,
          value: 120+ i*2,
          a: "5小时12分钟",
          b: 1400,
          c: 5800,
          d: 2000
        })
      }
     return mlist
    }
    /**
     * 跳入年详情月列表界面
     */
    const toYearDetial = (item) => {
      const itemJson = JSON.stringify(item)
      router.push({ path:'/year-momtion-detial-list', query: { itemJson }, title:"year-food-detial-list"})
    }

    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      yearLineList:[], // 年折线图
      SportList: [],
      endDate: undefined,
      todo: '',
      sumCount: 0,
      selectdate: new Date(),
      list: [],
      title: '本周数据',
      checked: false, // 是否开启图形分析
    })

    // 开始时间
    const getStart = (type) => {
      let startTime = ""
      switch (type){
        case 'week':
          startTime = moment(new Date(state.selectdate).setDate(new Date(state.selectdate).getDate() - 7) ).format('YYYY/MM/DD')
          break
        case 'month':
          startTime = moment(new Date(state.selectdate).setMonth(new Date(state.selectdate).getMonth() - 1) ).format('YYYY/MM/DD')
          break
        case 'year':
          startTime = moment(new Date(state.selectdate).setFullYear(new Date(state.selectdate).getFullYear() - 1) ).format('YYYY/MM/DD')
          break
      }
     return  startTime
    }

    // 图形分析开关
    const onUpdateValue = (newValue) => {
        state.checked = newValue;
    };

    const onchange=(data) =>{
      state.endDate = data
      requestList()
    }
    return {
      ...toRefs(state),
      onchange,
      onUpdateValue,
      toYearDetial
    }
  }
}
</script>

