<template>
  <!-- 列表数据展示-->
  <div class="his_record_box">
    <div v-if="type!=='year'">
      <van-collapse v-for="(item, index) in list" :key="index" v-model="item.active" accordion>
        <van-collapse-item  :title="item['date']" :name="1" >
          <div class="list_record"  >
            <div> <span>运动总时长</span> <span>{{ item['sportTimeSum']}}</span> </div>
            <div> <span>热量总消耗</span> <span>{{ item['heatSum']}}千卡</span> </div>
            <div> <span>总步数</span> <span>{{ item['stepNumberSum']}}步</span> </div>
            <div> <span>总距离</span> <span>{{ item['distanceSum']}}米</span> </div>
            <div class="list_record" v-for="(childItem, childIndex) in item['list']" :key="childIndex">
              <div> <span>{{ getSportName(childItem['sportType'])}}</span> <span>{{ getSportTime(childItem['sportTime'])}}</span> </div>
            </div>

          </div>
        </van-collapse-item>
      </van-collapse>
    </div>
    <div v-else-if="type=='year'" >
      <van-cell v-for="(item, index) in list"  :title="item['yearDate']" :key="index" is-link @click="toYearDetial(item)"/>
    </div>
  </div>

</template>

<script>
import {  reactive,  toRefs,} from 'vue'
import { formatMMTOHHMM } from "@/utils/util";
import {useStore} from "vuex";
export default {
  name: "index",
  props: {
    list: Array,
    type: String,
    toYearDetial: Function
  },
  setup(props){
    const store = useStore()
    const state = reactive({
    })

    const getSportTime = (time) => {
      return formatMMTOHHMM(time)
    }

    const getSportName = (value) => {
      console.log("sportTypeObj",value)
      console.log("sportTypeObj",store.state.sportTypeObj[`${ Number(value) }`])
      return store.state.sportTypeObj[`${ value }`]
    }
    // 加载更多
    const onLoad = () => {
    }
    return {
      ...toRefs(state),
      onLoad,
      getSportTime,
      getSportName
    }
  },
}
</script>

