<template>
  <div v-show="false">{{ option.series[0].data[0] || "" }}</div>
  <v-chart class="chart" :option="option" />
</template>

<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { LineChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent
} from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";
import {ref, defineComponent, onMounted} from "vue";

use([
  CanvasRenderer,
  LineChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent
]);

export default defineComponent({
  name: "HelloWorld",
  components: {
    VChart
  },
  props:{
    list: Array
  },
  provide: {
    [THEME_KEY]: "White"
  },
  setup: (props) => {

    onMounted(()=>{
      const { list } = props
      console.log("list11111",list[0])
      for (let i = 0; i < list.length; i++) {
        option.value.xAxis.data.push(list[i].date)
        option.value.series[0].data.push(list[i]['sportTimeSum'])
        option.value.series[1].data.push(list[i]['stepNumberSum'])
        option.value.series[2].data.push(list[i]['distanceSum'])
      }
      console.log( option.value.series)
    })

    const option = ref({
      title: {
        text: ''
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        data: ['热量(清卡)', '步数(步)', '距离(米)']
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data:[]
        // data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
      },
      yAxis: {
        type: 'value'
      },
      series: [
        {
          name: '热量(清卡)',
          type: 'line',
          stack: '总量',
          data: []
        },
        {
          name: '步数(步)',
          type: 'line',
          stack: '总量',
          data: []
        },
        {
          name: '距离(米)',
          type: 'line',
          stack: '总量',
          data: []
        },
      ]
    });
    return { option };
  }
});
</script>

<style scoped>
.chart {
  height: 400px;
}
</style>
